<template>
  <div id="main-container">
    <table class="responsive">
      <caption>
        {{ $route.meta.title }}
      </caption>
      <thead>
      <tr>
        <th scope="col">이름</th>
        <th scope="col">타입</th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td data-label="이름">
          <input-text
            v-model="figureUpdateForm.figureName"
          />
        </td>
        <td data-label="타입">
          <input-select
            :options="types"
            v-model="figureUpdateForm.type"
            class="select-box"/>
        </td>
      </tr>
      </tbody>
    </table>
    <table class="responsive">
      <caption>
        기타정보
      </caption>
      <thead>
      <tr>
        <th scope="col">등급</th>
        <th scope="col">별칭</th>
        <th scope="col">버전</th>
        <th scope="col">등장 시리즈</th>
        <th scope="col">세대</th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td data-label="등급">
          <input-select
            :options="grades"
            v-model="figureUpdateForm.grade"
            class="select-box"/>
        </td>
        <td data-label="별칭">
          <input-text
            v-model="figureUpdateForm.nickName"
          />
        </td>
        <td data-label="버전">
          <input-text
            v-model="figureUpdateForm.version"
          />
        </td>
        <td data-label="등장 시리즈">
          <input-text
            v-model="figureUpdateForm.series"
          />
        </td>
        <td data-label="세대">
          <input-text
            v-model="figureUpdateForm.generation"
            />
        </td>
      </tr>
      </tbody>
    </table>
    <table class="responsive">
      <caption>
        구매정보
      </caption>
      <thead>
      <tr>
        <th scope="col">가격</th>
        <th scope="col">구매일자</th>
        <th scope="col">구매처</th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td data-label="가격">
          <input-text
            v-model="figureUpdateForm.price"
            oninput="this.value = this.value.replace(/^0+|\D+/g, '').replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,');"
          />

        </td>
        <td
          data-label="구매일자">
          <DatePicker
            name="date"
            v-model="figureUpdateForm.date"
            placeholder="구매일자"
          />
        </td>
        <td data-label="구매처">
          <input-text
            v-model="figureUpdateForm.store"
          />
        </td>
      </tr>
      </tbody>
    </table>
    <table class="responsive">
      <caption>
        제작정보
      </caption>
      <thead>
      <tr>
        <th scope="col">시작일자</th>
        <th scope="col">마감일자</th>
        <th scope="col">완료여부</th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td
          data-label="시작일자">
          <DatePicker
            name="startDate"
            v-model="figureUpdateForm.startDate"
            placeholder="시작일자"
          />
        </td>
        <td
          data-label="마감일자">
          <DatePicker
            name="endDate"
            v-model="figureUpdateForm.endDate"
            placeholder="마감일자"
          />
        </td>
        <td data-label="완료여부">
          <input-select
            :options="completes"
            v-model="figureUpdateForm.completeYn"
            class="select-box"/>
        </td>
      </tr>
      </tbody>
    </table>
    <div class="input-wrapper" style="margin: 10px 0 20px 0;">
      <button
        @click="_updateFigure"
        class="basic__button"
      >
        수정하기
      </button>
      <button
        class="cancel__button"
        @click="_deleteFigure"
      >
        삭제
      </button>
      <button
        class="cancel__button"
        @click="_routeToGunplaList"
      >
        뒤로가기
      </button>
    </div>
  </div>
</template>

<script>
import DatePicker from '@/components/date-picker.vue'
import { mapState } from 'pinia'
import { usersStore } from '@/store/users'
import { figureStore } from '@/store/figures'
import Constants from '@/Constants'
import InputText from '@/components/global/input-text'
import sweetAlert from '@/wrapper/sweet-alert'

export default {
  name: 'user-gunpla-detail',
  components: { InputText, DatePicker },
  computed: {
    ...mapState(usersStore, ['userCustomInfo']),
    ...mapState(figureStore, ['getFigureInfo'])
  },
  data () {
    return {
      types: Constants.TYPES,
      grades: Constants.GRADES,
      completes: Constants.Completes,
      figureUpdateForm: {
        figureOid: 0,
        figureName: null,
        type: { code: '', value: '' },
        wishYn: false,
        nickName: null,
        version: null,
        series: null,
        grade: { code: '', value: '' },
        generation: null,
        price: 0,
        date: null,
        store: null,
        startDate: null,
        endDate: null,
        completeYn: { code: '', value: '' }
      }
    }
  },
  methods: {
    _routeToGunplaList () {
      this.$router.push({ name: 'user-gunpla-list' })
    },
    _updateFigure () {
      if (this.figureUpdateForm.figureName.replace(/ /g, '') === '') {
        sweetAlert.noIcon('이름은 필수 입력 사항입니다.', '확인')
      } else {
        const figureUpdateIn = {
          figureOid: this.figureUpdateForm.figureOid,
          figureName: this.figureUpdateForm.figureName,
          type: this.figureUpdateForm.type,
          wishYn: this.figureUpdateForm.wishYn,
          nickName: this.figureUpdateForm.nickName,
          version: this.figureUpdateForm.version,
          series: this.figureUpdateForm.series,
          grade: this.figureUpdateForm.grade,
          generation: this.figureUpdateForm.generation,
          price: String(this.figureUpdateForm.price).replaceAll(',', ''),
          date: this.figureUpdateForm.date,
          store: this.figureUpdateForm.store,
          startDate: this.figureUpdateForm.startDate,
          endDate: this.figureUpdateForm.endDate,
          completeYn: this.figureUpdateForm.completeYn
        }
        this.updateFigure(this.userCustomInfo.userOid, figureUpdateIn).then(() => {
          sweetAlert.noIcon(`${figureUpdateIn.figureName} 수정 되었습니다 :)`)
          this._routeToGunplaList()
        })
      }
    },
    _deleteFigure () {
      sweetAlert.question(null, '수집 정보를 삭제하시겠습니까?', '삭제', '아니오').then(con => {
        if (con.value) {
          this.deleteFigure(this.userCustomInfo.userOid, this.figureUpdateForm.figureOid).then(() => {
            sweetAlert.noIcon('수집 정보가 삭제되었습니다.', '확인')
            this._routeToGunplaList()
          })
        }
      })
    }
  },
  created () {
    this.figureUpdateForm = this.getFigureInfo
  },
  mounted () {
    this.figureUpdateForm.type = this.getFigureInfo.type.code
    this.figureUpdateForm.grade = this.getFigureInfo.grade.code
    this.figureUpdateForm.completeYn = this.getFigureInfo.completeYn.code
    this.figureUpdateForm.price = String(this.figureUpdateForm.price)
      .replace(/^0+|\D+/g, '').replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')
  }
}
</script>
<style scoped lang="scss">
.grid-item {
  height: 160px !important;
}
.button__wrapper {
  margin-top: 30px;
  text-align: right;
}
</style>
