import { useCookies } from 'vue3-cookies'
import { defineStore } from 'pinia'
const { cookies } = useCookies()

export const figureStore = defineStore('figures', {
  state: () => ({
    figureList: cookies.get('figureList') ? cookies.get('figureList') : {},
    figureInfo: cookies.get('figureInfo') ? cookies.get('figureInfo') : {},
    figureWishList: cookies.get('figureWishList') ? cookies.get('figureWishList') : {},
    figureWishInfo: cookies.get('figureWishInfo') ? cookies.get('figureWishInfo') : {}
  }),
  getters: {
    getFigureList: (_state) => _state.figureList,
    getFigureInfo: (_state) => _state.figureInfo,
    getFigureWishList: (_state) => _state.figureWishList,
    getFigureWishInfo: (_state) => _state.figureWishInfo
  },
  actions: {
    async setFigureList (data) {
      cookies.remove('figureList')
      await this.$patch((state) => {
        state.figureList = data
        cookies.set('figureList', data)
      })
    },
    async setFigureInfo (data) {
      cookies.remove('figureInfo')
      await this.$patch((state) => {
        state.figureInfo = data
        cookies.set('figureInfo', data)
      })
    },
    async setWishFigureList (data) {
      cookies.remove('figureWishList')
      await this.$patch((state) => {
        state.figureWishList = data
        cookies.set('figureWishList', data)
      })
    },
    async setFigureWishInfo (data) {
      cookies.remove('figureWishInfo')
      await this.$patch((state) => {
        state.figureWishInfo = data
        cookies.set('figureWishInfo', data)
      })
    }
  }
})
